/* ---- Estilo general ---- */
body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f4f6f9;
  color: #222;
}

.container {
  max-width: 1200px;
  margin: 40px auto;
  padding: 20px;
}

/* ---- Menu ---- */
nav {
  background: linear-gradient(90deg, #0d1b2a, #1b263b);
  padding: 12px 20px;
  position: sticky;
  top: 0;
  z-index: 10;
}

nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 20px;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  transition: color 0.2s;
}

nav ul li a:hover {
  color: #00b4d8;
}

/* ---- TÃ­tulos ---- */
h2 {
  text-align: center;
  margin-bottom: 30px;
  color: #0d1b2a;
}

/* ---- Servicios ---- */
.servicios-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 25px;
  margin-top: 20px;
}

.card {
  background: #fff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
  transition: transform 0.2s ease;
}

.card:hover {
  transform: translateY(-5px);
}

.card img {
  max-width: 100%;
  border-radius: 10px;
  margin-bottom: 15px;
}

.precio {
  font-weight: bold;
  font-size: 1.1em;
  color: #0077cc;
  display: block;
  margin-top: 10px;
}

/* ---- Contacto ---- */
.contacto-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-top: 20px;
  align-items: start;
}

.contacto-grid form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.contacto-grid input,
.contacto-grid textarea {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #ccc;
  font-size: 14px;
}

.contacto-grid button {
  background: #0077cc;
  color: #fff;
  border: none;
  padding: 10px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s;
}

.contacto-grid button:hover {
  background: #005fa3;
}

.mapa iframe {
  border-radius: 12px;
  width: 100%;
  height: 100%;
  min-height: 300px;
}

/* ---- Responsive ---- */
@media (max-width: 768px) {
  .contacto-grid {
    grid-template-columns: 1fr;
  }
}
